import { convertTrack } from './index'

// Other plugins.
import { logManager } from '../../logs'

const log = logManager.getLogger('PROXY')

/**
 * Media "converter".
 * Receives webRTC commands intended for a Media object.
 * Converts the command into the actual operation, and converts
 *    the operation's return value into a format that the Proxy
 *    Stack can use.
 * @method media
 * @param {Object} webRTC The local webRTC stack.
 * @param {Object} command A webRTC command.
 * @return {Promise} Resolves when the webRTC operation has completed.
 */
export default async function media(webRTC, command) {
  const { id, operation, params } = command
  const media = webRTC.managers.media.get(id)

  if (!media) {
    /*
     * Because of channel latency, `media` may have been cleaned-up since this command was sent.
     *    If so, try to return an appropriate "default" response.
     */
    log.warn(`Media ${id} no longer exists; attempting to send appropriate response for ${operation}.`)
  }

  if (operation === 'getTracks') {
    return media ? media.getTracks().map(convertTrack) : []
  } else {
    // General case: Don't convert the return.
    return media ? media[operation](...params) : undefined
  }
}
